const { default: axios } = require("axios");

const api = axios.create({
  url: "http://127.0.0.1:8080/api/v1",
  withCredentials: true,
});

export async function getCabins() {
  try {
    const data = await axios("http://127.0.0.1:8080/api/v1/cabins");
    return data;
  } catch (err) {
    console.log(err);
  }
}
